import React, { Component } from 'react';

import buttonList from '../initialButtonList';
import Layout from '../containers/Layout';
import SocialButtonList from './SocialButtonList';
import { auth } from '../firebase';

class Home extends Component {
  /**
   * Send the user to 'Dashboard' if is authenticated
   */
  componentDidMount() {
    auth.getAuth().onAuthStateChanged(user => {
      if (user) {
        this.props.history.push('/');
      }
    });
    this.fetchData()
  }

  titleCase = (str) => {
    str = str.split('-').join(' ')
    var splitStr = str.toLowerCase().split(' ');
    for (var i = 0; i < splitStr.length; i++) {
      // You do not need to check if i is larger than splitStr length, as your for does that for you
      // Assign it back to the array
      splitStr[i] = splitStr[i].charAt(0).toUpperCase() + splitStr[i].substring(1);
    }
    // Directly return the joined string
    return splitStr.join(' ');
  }

  listOfChallenges = (challenges) => {
    var component = document.getElementById('listOfChallenges')
    challenges.forEach(challenge => {
      if (challenge.name.includes('challenge-', 0)) {
        var challengeComponent = document.createElement('challenge')
        challengeComponent.setAttribute('style', 'border-color: rgb(78,121,167)')
        challengeComponent.setAttribute('class', 'svelte-m9wvyg')

        var h2 = document.createElement('h2')
        h2.setAttribute('class', 'svelte-m9wvyg')
        var a = document.createElement('a')
        a.setAttribute('href', 'contest/' + challenge.name.split('-')[1])
        a.setAttribute('style', 'text-decoration:none; color:white')
        var nameOfChallenge = document.createTextNode(this.titleCase(challenge.name))
        a.append(nameOfChallenge)
        h2.append(a)
        challengeComponent.append(h2)

        var p = document.createElement('p')
        p.setAttribute('class', 'svelte-m9wvyg')
        var number = document.createTextNode('0')
        p.append(number)
        challengeComponent.append(p)

        component.append(challengeComponent)
      }
    })
  }

  fetchData() {
    const authorizationBasic = window.btoa(process.env.REACT_APP_APIKEY);
    const header = new Headers();
    header.set('Authorization', 'Basic ' + authorizationBasic)
    fetch('https://api.github.com/repos/minecode/code_contest/branches', {
        method: 'GET',
        headers: header
      })
      .then(res => res.json())
      .then(data => {
        this.listOfChallenges(data)
      })
  }

  render() {
    return (
      <Layout contentCenter={true}>
        { auth.getAuth().currentUser === null ?
          <SocialButtonList buttonList={buttonList} auth={auth.getAuth} />
          :
          null
        }
        <div className="container text-center">
          <h2 className="p-5 m-5 b-5 text-center">Challenges</h2>
          <main
            className="row svelte-m9wvyg p-5 m-5 b-5"
            id="listOfChallenges"></main>
        </div>
      </Layout>
    );
  }
}

export default Home;



