import React from 'react';
import PropTypes from 'prop-types';
import { auth } from '../firebase';

import './Layout.css';

const propTypes = {
  children: PropTypes.node.isRequired,
  contentCenter: PropTypes.bool
};

const defaultProps = {
  contentCenter: false
};

const Layout = ({ children, contentCenter }) => {

  return (
    <div className="container">
      <nav className="navbar navbar-expand-lg navbar-dark">
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarTogglerDemo01" aria-controls="navbarTogglerDemo01" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarTogglerDemo01">
          <a className="navbar-brand navbar-nav mr-auto mt-2 mt-lg-0" href="/">Code Contest</a>
          <form className="form-inline my-2 my-lg-0">
          { auth.getAuth().currentUser !== null ?
            <button
                className="btn__logout"
                onClick={() => auth.getAuth().signOut()}>
                Logout
            </button> 
            : 
            null
          }
          </form>
        </div>
      </nav>
      
      <main className={contentCenter ? 'content-center' : ''}>{children}</main>
    </div>
  );
};

Layout.propTypes = propTypes;
Layout.defaultProps = defaultProps;

export default Layout;



