import React, { Component } from 'react';
import Layout from '../containers/Layout';
import './Contest.css';

class Contest extends Component {

  componentDidMount() {
    this.fetchData()
  }

  fetchData() {
    const authorizationBasic = window.btoa(process.env.REACT_APP_APIKEY);
    const header = new Headers();
    header.set('Authorization', 'Basic ' + authorizationBasic)
    fetch('https://api.github.com/repos/minecode/code_contest/branches/challenge-' + this.props.match.params.id, {
        method: 'GET',
        headers: header
      })
      .then(res => res.json())
      .then(data => {
        console.log(data)
      })
  }

  render() {
    return (
      <Layout>
        <div id="contestName"></div>
      </Layout>
    );
  }
}

export default Contest;



